<template>
    <views class="improve-info">
        <nav-bar
            :title="moduleTitle"
            fixed
            @click-left="onClickLeft">
        </nav-bar>
        <views class="improve-info-content">
            <views
                v-for="(item, index) in list"
                :key="index">
                <info-item v-model="list[index]"></info-item>
            </views>
        </views>
        <views class="improve-info-button">
            <button class="primary" @click="save">保存</button>
        </views>
    </views>
</template>

<script>
import NavBar from '@/components/navBar'
import InfoItem from './InfoItem'
import tools from '@/mixins/tools'
import api from '@/utils/api'
export default {
    components: {
        NavBar,
        InfoItem
    },
    mixins: [tools],
    data() {
        return {
            moduleTitle: '完善资料',
            list: [],
            defaultList: [
                {
                    id: '44444',
                    type: 4,
                    name: '种植信息'
                },
                {
                    id: '3333',
                    type: 3,
                    name: '种植亩数（亩）',
                    tip: '提示',
                    default_value: '50',
                    choose_value: [],
                    is_required: true,
                    value: ''
                },
                {
                    id: '222222',
                    type: 2,
                    name: '用费时间（月）',
                    tip: '提示',
                    default_value: '50',
                    choose_value: [],
                    is_required: true,
                    value: ''
                },
                {
                    id: '11111',
                    type: 1,
                    name: '年够肥次数',
                    tip: '提示',
                    default_value: '选项2',
                    choose_value: ['选项1', '选项2', '选项3'],
                    is_required: true,
                    value: ''
                },
                {
                    id: '5555',
                    type: 4,
                    name: '消费信息'
                },
                {
                    id: '666',
                    type: 1,
                    name: '年总够肥数量',
                    tip: '提示',
                    default_value: '',
                    choose_value: ['选项1', '选项2', '选项3'],
                    is_required: true,
                    value: ''
                }
            ]
        }
    },
    mounted() {
        this.setBgColor()
        this.getMemberCustom()
    },
    destroyed() {
        this.resetBgColor()
    },
    methods: {
        onClickLeft() {
            this.back()
        },
        getMemberCustom() {
            api.getMemberCustom().then((res) => {
                // console.log(res)
                this.list = this.setDefaultValue(this.defaultList)
            })
        },
        setDefaultValue(arr) {
            const _arr = []
            arr.map((item) => {
                _arr.push({
                    ...item,
                    value: item.default_value
                })
            })
            return _arr
        },
        save() {
            const a = this.list.filter(item => item.type < 4 && !item.value && item.is_required)
            if (a.length > 0) return this.notify('有必填项未填写')
            const params = {
                list: this.list.filter(item => item.type < 4 && item.value).map(item => ({
                    id: item.id,
                    value: item.value
                }))
            }
            api.saveCustom(params).then(() => {
                this.sucMsg('保存成功')
            })
        }
    }
}
</script>

<style lang="scss" scoped>
@import './index.scss';
</style>
